import { MutableRefObject, forwardRef, useEffect, useImperativeHandle, useState } from 'react';
import styles from './MapBox.module.scss';
import SceneComponent from './components/SceneComponent/SceneComponent';
import { isMapIt2Response, MapIt2Response, MapObj, MapObjToSave } from "./mapitApiTypes";
import { IJsonConfig } from "./types";
import MeshObjectContextProvider from 'src/contexts/MeshObjectContextProvider';
import 'bootstrap/dist/css/bootstrap.min.css';
import MapBoxContextProvider from './contexts/MapBoxContextProvider';
import { Provider } from 'react-redux';
import { store } from './store';
import { AllAmenities } from './helpers/amenities.helper';
import SVGIcons from './helpers/svgIcons.helper';

export type MapBoxRefFs = {
  refreshData: () => void,
  createRouteToAmenity: (amenityId: AmenityID) => void;
  createRouteToStore: (retailerSlug: string) => void;
  getInitialDistance: () => number;
  resetMap: () => void,
}

export type AmenityID = 'atm' | 'management' | 'playarea' | 'restroom' | 'family-restroom' | 'elevator' | 'security' | 'dog-poop-station' | 'child-stroller' | 'vending-machine' | 'stairs' | 'charging-station' | 'wheelchair';

export interface IAppProps {
  mapitData?: unknown;
  config: Partial<IJsonConfig>;
  stats?: boolean;
  onSettingsLoading?: (settings: MapIt2Response) => void;
  webApiURI?: string;
  mediaStorageURI?: string;
  onSubmit?: (data: MapObjToSave, refreshData?: () => void) => void;
  refObj?: MutableRefObject<MapBoxRefFs | null> | undefined,
  onResetData?: (data: MapObj) => void;
  deviceType?: string | "";
  setCameraControlPosition?: (cameraControlPos: any) => void;
  onExtractedAmenities?: (amenityIds: AmenityID[]) => void;
  handleClickOnRetailer?: (slug: string) => void;
}


const MapBox = forwardRef(({
  mapitData,
  config,
  onSettingsLoading,
  webApiURI,
  mediaStorageURI,
  onSubmit,
  refObj,
  deviceType,
  setCameraControlPosition,
  onResetData,
  onExtractedAmenities,
  handleClickOnRetailer
}: IAppProps, ref) => {
  const [selectedActiveObjectId, setSelectedActiveObjectId] = useState<string>('');
  const [mapData, setMapData] = useState<any>(undefined);
  useImperativeHandle(ref, () => {
    return {
      getInitialDistance: refObj?.current?.getInitialDistance
    }
  });

  useEffect(() => {
    setMapData(mapitData);
  }, [mapitData])

  if (!mapitData && !config.CENTER_ID) {
    console.error('Please provide either mapitData or CENTER_ID');
    return null;
  }
  if (!config.CENTER_ID && !isMapIt2Response(mapitData)) {
    console.error('mapitData is not a valid MapIt2Response');
    return null;
  }

  const handleOnSubmit = (data: MapObjToSave, refreshData?: () => void) => {
    if (onSubmit) {
      onSubmit(data, refreshData);
    }
  }

  return (
    <Provider store={store}>
      <MapBoxContextProvider>
        <MeshObjectContextProvider>
          <div className={`${styles['mapbox-component']} MAP_ROLE_${config.ROLE} ${config.ROLE !== 'PORTAL' ? styles.view : ''}`}>
            <SceneComponent
              ref={refObj}
              setSelectedActiveObjectId={setSelectedActiveObjectId}
              selectedActiveObjectId={selectedActiveObjectId}
              mapitData={mapData}
              config={config}
              onSettingsLoading={onSettingsLoading}
              webApiURI={webApiURI}
              mediaStorageURI={mediaStorageURI}
              onSubmit={handleOnSubmit}
              onResetData={onResetData}
              deviceType={deviceType}
              setCameraControlPosition={setCameraControlPosition}
              onExtractedAmenities={onExtractedAmenities}
              handleClickOnRetailer={handleClickOnRetailer}
            />
            <div className={styles.hide}>
              <div id={`map-special-svg-kiosk`} dangerouslySetInnerHTML={{ __html: SVGIcons.Kiosk }}></div>
              <div id={`map-special-svg-escalator`} dangerouslySetInnerHTML={{ __html: SVGIcons.Escalator }}></div>

              {AllAmenities.map(amenity => (
                <div id={`map-special-svg-${amenity.type}`} dangerouslySetInnerHTML={{ __html: amenity.svgCode }}></div>
              ))}
            </div>
          </div>
        </MeshObjectContextProvider>
      </MapBoxContextProvider>
    </Provider>


  );
})

export default MapBox;
